@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
nav {
	width: 100%;
	height: 50px;
	background-color: #6060d1;
	color: #fff;
	display: table;
	overflow: hidden;
	border-radius: 10px;
	box-shadow: 0 5px 15px 0 rgb(187, 176, 250);
}

.HeaderTitle {
	background-color: rgb(84, 55, 230);
}

.HeaderContent {
	width: auto;
	height: 100%;
	display: table-cell;
	vertical-align: middle;
	font-family: "Roboto", sans-serif;
}

.HeaderOption {
	font-size: 16px;
	width: 27%;
	transition: 0.2s linear;
	opacity: 0.8;
}

.HeaderOption button {
	width: 100%;
	height: 100%;
	background: none;
	border: none;
	padding: 0;
	color: #fff;
	font-size: 16px;
}

.HeaderOption:last-child {
	width: 19%;
}

.HeaderOption button:hover {
	cursor: pointer;
}

.HeaderOption:hover {
	background-color: rgb(84, 55, 230);
	opacity: 1;
}

.discoverable {
	position: relative;
}

#algoBtn::before {
	content: "Algorithm";
}

#sortBtn::before {
	content: "Sort";
}

.discoverable::before {
	position: absolute;
	top: 50%;
	left: 50%;
	opacity: 1;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.discoverable .actualValue {
	position: absolute;
	top: 50%;
	left: 50%;
	opacity: 0;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.discoverable:hover .actualValue {
	opacity: 1;
}

.discoverable:hover::before {
	opacity: 0;
}

.HeaderAdditionalInput {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	margin-top: 15px;
}

input[type="range"] {
	overflow: hidden;
	width: 150px;
	-webkit-appearance: none;
	background-color: #bbb;
	border-radius: 5px;
}

input[type="range"]::-webkit-slider-runnable-track {
	margin: 0;
	height: 12px;
	-webkit-appearance: none;
	color: #735ceb;
}

input[type="range"]::-webkit-slider-thumb {
	margin: 0;
	width: 12px;
	-webkit-appearance: none;
	height: 12px;
	cursor: ew-resize;
	background: #434343;
	box-shadow: -80px 0 0 80px #735ceb;
}

input[type="radio"]:hover {
	cursor: pointer;
}

input[type="radio"]:after {
	width: 15px;
	height: 15px;
	border-radius: 15px;
	top: -2px;
	left: -1px;
	position: relative;
	background-color: #d1d3d1;
	content: "";
	display: inline-block;
	visibility: visible;
	border: 2px solid #f2f2f2;
}

input[type="radio"]:checked:after {
	width: 15px;
	height: 15px;
	border-radius: 15px;
	top: -2px;
	left: -1px;
	position: relative;
	background-color: #735ceb;
	content: "";
	display: inline-block;
	visibility: visible;
	border: 2px solid #f2f2f2;
}
.Main {
	margin-top: 15px;
	width: 100%;
	height: 503px;
	background-color: #fff;
	box-shadow: 0 5px 15px 0 #ddd;
	border-radius: 10px;
	padding: 7px 0;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: flex-end;
	        align-items: flex-end;
	-webkit-justify-content: center;
	        justify-content: center;
}
.Bar {
	width: 24px;
	border-radius: 12px 12px 0 0;
	border: 1px solid #ddd;
	margin: 0 5px;
	transition: 0.2s linear;
}
.App {
  text-align: center;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #f2f2f2;
}
